import React, { useEffect, useState, useRef } from 'react';

const DiceAnimation = ({ isAnimating, onAnimationEnd, finalImage, currentcolor }) => {
  const [currentImage, setCurrentImage] = useState(0);
  const [animationComplete, setAnimationComplete] = useState(false);
  const animationFrameIdRef = useRef(null);
  const startTimeRef = useRef(null);
  const frameRate =  7; // Number of frames per second
  const frameDuration = 700 / frameRate; // Duration of each frame in milliseconds

  useEffect(() => {
    if (!isAnimating) {
      if (animationFrameIdRef.current) {
        cancelAnimationFrame(animationFrameIdRef.current);
        animationFrameIdRef.current = null;
      }
      setAnimationComplete(true);
      return;
    }

    setAnimationComplete(false);
    startTimeRef.current = performance.now();
    const totalImages = 13;
    const duration = 500; // Total duration of the animation in milliseconds

    const animate = (timestamp) => {
      const elapsed = timestamp - startTimeRef.current;
      const progress = Math.min(elapsed / duration, 1);

      // Update image index based on frame duration
      const frameIndex = Math.floor(elapsed / frameDuration) % totalImages;
      setCurrentImage(frameIndex);

      if (progress < 1) {
        animationFrameIdRef.current = requestAnimationFrame(animate);
      } else {
        setAnimationComplete(true);
        setCurrentImage(finalImage); // Set finalImage at the end of animation
        onAnimationEnd();
      }
    };

    animationFrameIdRef.current = requestAnimationFrame(animate);

    return () => {
      if (animationFrameIdRef.current) {
        cancelAnimationFrame(animationFrameIdRef.current);
        animationFrameIdRef.current = null;
      }
    };
  }, [isAnimating, finalImage, onAnimationEnd]);

  const padNumber = (num) => num.toString().padStart(3, '0');

  return (
    <div className="dice-roll">
      {animationComplete ? (
        <div className="diceimg" style={{ position: 'relative', display: 'inline-block' }}>
  <img
    src={`${process.env.PUBLIC_URL}/diceimg/diceimg_f/dice${finalImage}.png`}
    alt={`Dice ${finalImage}`}
    style={{ display: 'block' }} 
  />
  <span 
    style={{ 
      position: 'absolute', 
      top: '50%', 
      left: '50%', 
      transform: 'translate(-50%, -50%)', 
      color: currentcolor === 'yellow' ? 'yellow' : currentcolor === 'green' ? 'lawngreen' : currentcolor === 'blue' ? 'aqua' :  currentcolor === 'red' ? 'red':  'aqua',
      fontFamily: 'Courier', 
      fontSize: '34px' ,
      fontStyle: 'italic',
      fontWeight:'bold'
    }}
  >
    {finalImage}
  </span>
</div>

      ) : (
        <div  className = "diceimg">
          <img 
            src={`${process.env.PUBLIC_URL}/diceimg/dice${padNumber(currentImage)}.png`}
            alt={`Dice ${currentImage}`}
          />
          <br />
          <span style={{ fontFamily: "Nasty Works Company Regular", fontSize: "48px" }}>
            {/* {currentImage} */}
          </span>
        </div>
      )}
    </div>
  );
};

export default DiceAnimation;
